.nav {
    z-index: +2;
    width: 100%;
    position: fixed;
    top: 0;
    &__menu {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        margin: 0;
        padding-left: 9vw;
        padding-right: 9vw;
        background-color: white;
        @media (max-width: 1200px) {
            display: none;
            flex-direction: column;
            &--visible {
              display: block;
              font-size: 16px;
              line-height: 2em;
              text-align: center;
              margin: 0;
              padding: 0;
              border: 0;
              list-style-position: inside;
              margin-top: 85px;
              flex-direction: column;
            }
          }
    }
    &__logo {
        height: 30vh;
        width: auto;
        margin-top: -9vh;
        margin-bottom: -9vh;
    }
    &__link {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 8vw;
        height: 4vh;
        font-family: "Montserrat", sans-serif;
        font-size: 1.1rem;
        text-decoration: none;
        color: #ab5d55;
    }
    &__switcher {
        position: fixed;
        font-size: 2rem;
        background-color: white;
        line-height: 2em;
        border: 0;
        right: 65px;
        top: 2vh;
        cursor: pointer;
        @media (min-width: 1201px) {
            display: none;
        }
    }
    &__button {
        transition: all 0.3s;
        list-style-type: none;
        border: 2px solid rgba($color: #c1a6b5, $alpha: 0.9);
        &:hover {
            transition: all 0.3s;
            border: 2px solid rgba($color: #6b4964, $alpha: 0.9);
        }
        &--active {
            border: 2px solid rgba($color: #6b4964, $alpha: 0.9);
        }
    }
}
